<template>
  <div class="search-box">
    <a-form-model
      layout="inline"
      :model="searchForm"
      @submit="handleSubmit"
      @submit.native.prevent
    >
      <a-form-model-item label="搜索关键字">
        <a-input
          allowClear
          v-model="searchForm.searchWord"
          placeholder="请输入关键字"
        ></a-input>
      </a-form-model-item>
      <a-form-model-item label="商品类目">
        <a-select
          allowClear
          show-search
          placeholder="请选择商品类目"
          style="width: 200px"
          @change="handleChange"
        >
          <a-select-option v-for="c in categoryList" :key="c.id" :value="c.id">
            {{ c.name }}
          </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item>
        <a-button type="primary" html-type="submit">搜索</a-button>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>
<script>
import api from '@/api/category';

export default {
  created() {
    // 组件创建时调用商品类目接口获取商品类目数据
    api.list().then((res) => {
    //   console.log(res);
      this.categoryList = res.data;
    });
  },
  data() {
    return {
      searchForm: {
        searchWord: '',
        category: '',
      },
      // 存储商品类目列表数据
      categoryList: [],
    };
  },
  methods: {
    // 提交表单时触发的函数
    handleSubmit() {
      // 提交表单时要把 searchForm里的字段传递到父组件中
      this.$emit('submit', this.searchForm);
    },
    // 切换类目时触发的函数
    handleChange(val) {
    // 切换到点击的类目
      this.searchForm.category = val;
    },
  },
};
</script>
<style lane='less' scoped>
.search-box {
  padding: 10px 30px;
}
</style>
