<template>
  <div class="productList">
    <!-- 搜索 -->
    <search-box @submit="searchSubmit"></search-box>
    <a-button  class="addBtn" type="primary">
      <router-link :to="{name:'ProductAdd'}">新增商品</router-link>
    </a-button>
    <!-- 表格 -->
    <products-table
    :data='tableData'
    :page="page"
    @change="changePage"
    :categoryList="categoryList"
    @edit="editProduct"
    @remove="removeProduct"
    ></products-table>
  </div>
</template>

<script>
import SearchBox from '@/components/Search.vue';
import ProductsTable from '@/components/ProductsTable.vue';
import api from '@/api/product';
import categoryApi from '@/api/category';

export default {
  data() {
    return {
      searchForm: [],
      // 接收表格数据
      tableData: [],
      // 翻页
      page: {
        current: 1,
        pageSize: 10,
        showSizeChanger: true,
        total: 1,
      },
      categoryList: [],
      categoryObj: {},
    };
  },
  components: {
    SearchBox,
    ProductsTable,
  },
  methods: {
    searchSubmit(form) {
      console.log(form);
      // 提交表单时把searchform里的关键字和类目传递过来
      this.searchForm = form;
    },
    // 在父组件中获取表格数据 子组件是ProductsTable.vue
    getTableData() {
      api.list({
        page: this.page.current,
        size: this.page.pageSize,
        ...this.searchForm,
      }).then((res) => {
        // console.log(res);
        this.page.total = res.total;
        this.tableData = res.data;
        this.tableData = res.data.map((item) => ({
          ...item,
          categoryName: this.categoryObj[item.category].name,
        }));
      });
    },
    // 翻页时触发
    changePage(page) {
      this.page = page;
      this.getTableData();
    },
    editProduct(record) {
      this.$router.push({
        name: 'ProductEdit',
        params: {
          id: record.id,
        },
      });
    },
    removeProduct(record) {
      this.$confirm({
        title: '确认删除',
        content: () => <div style="color:red;">{ `确认删除标题为"${record.title}"的产品吗？` }</div>,
        onOk: () => {
          api.remove({
            id: record.id,
          }).then(() => {
            this.getTableData();
          });
        },
        onCancel() {
          console.log('Cancel');
        },
        class: 'confirm-box',
      });
    },
  },
  async created() {
    this.getTableData();
    await categoryApi.list().then((res) => {
      this.categoryList = res.data;
      res.data.forEach((item) => {
        this.categoryObj[item.id] = item;
      });
    });
  },
};
</script>

<style lang="less" scoped>
.productList{
  position: relative;
  .addBtn{
    position: absolute;
    top: 14px;
    right: 30px;
  }
}
</style>
