<template>
  <a-table
  :data-source="tableData"
  :columns="columns"
  :pagination="page"
  @change="changePage">
    <div slot="operation" slot-scope="text,record">
        <a-button @click="editProduct(record)">编辑</a-button>
        <a-button @click="removeProduct(record)">删除</a-button>
    </div>
  </a-table>
</template>
<script>

const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    align: 'center',
  },
  {
    title: '标题',
    dataIndex: 'title',
    key: 'title',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '描述',
    dataIndex: 'desc',
    key: 'desc',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '类目',
    dataIndex: 'categoryName',
    key: 'category',
    ellipsis: true,
    align: 'center',
  },

  {
    title: '预售价格',
    dataIndex: 'price',
    key: 'price',
    align: 'center',
  },
  {
    title: '折扣价',
    dataIndex: 'price_off',
    key: 'price_off',
    align: 'center',
  },
  {
    title: '标签',
    dataIndex: 'tags',
    key: 'tags',
    align: 'center',
  }, {
    title: '库存', // 限制购买数量
    dataIndex: 'inventory',
    key: 'inventory',
    align: 'center',
  }, {
    title: '上架状态',
    dataIndex: 'status',
    key: 'status',
    customRender(text, record) {
      return record.status === 1 ? '上架' : '下架';
    },
    align: 'center',
  },
  {
    title: '操作',
    dataIndex: 'operation',
    key: 'operation',
    width: '200px',
    scopedSlots: { customRender: 'operation' },
    align: 'center',
  },
];
export default {
  data() {
    return {
      columns,
    };
  },
  props: ['data', 'page'],
  // 用于解决key值不唯一的问题
  computed: {
    tableData() {
      return this.data.map((item) => ({
        ...item,
        key: item.id,
      }));
    },
  },
  methods: {
    // 切换页码
    changePage(page) {
      this.$emit('change', page);
    },
    editProduct(record) {
    //   console.log(record);
      this.$emit('edit', record);
    },
    removeProduct(record) {
      console.log(record);
      this.$emit('remove', record);
    },
  },
};
</script>
